html,
body {
  height: 100%;
  font-size: 1rem;
}
body {
  margin: 0;
  font-family: Poppins;
}

* {
  box-sizing: border-box;
  outline: none;
  font-family: Poppins;
}

.mat-body,
.mat-body-1,
.mat-typography {
  font-size: 16px;
}

.ag-root-wrapper {
  background: #f8f8f8;
  box-shadow: -4px 0px 3px rgba(0, 0, 0, 0.1), 4px 0px 3px rgba(0, 0, 0, 0.15),
    0px -4px 3px rgba(0, 0, 0, 0.1), 0px 4px 3px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
}

// scrollbar css

::-webkit-scrollbar {
  width: 8px !important;
  height: 8px !important;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px !important;
  -webkit-box-shadow: inset 0 0 6px #343a40 !important;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 30%) !important;
  border-radius: 10px !important;
}

// button
.mat-button,
.mat-raised-button,
.mat-icon-button,
.mat-stroked-button,
.mat-flat-button,
.mat-fab,
.mat-mini-fab .mat-body,
.mat-body-1,
.mat-typography {
  font-size: inherit;
}

button[class~="round"] {
  border-radius: 50px !important;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px !important;
  -webkit-box-shadow: inset 0 0 6px #343a40 !important;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 30%) !important;
  border-radius: 10px !important;
}
// model
// .mat-dialog-container {
//   padding: 0 !important;
// }

// Ag grid

.grid-cell {
  border-left: 1px solid #dee2e6 !important;
  border-right: 1px solid #dee2e6 !important;
}

.ag-header-cell {
  padding: 0 0.5rem !important;
}

.ag-header-group-cell-label,
.ag-header-cell-label {
  justify-content: center;
}

.ag-header-cell-text {
  white-space: pre-line !important;
}
.ag-root-wrapper {
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25) !important;
  border-radius: 7px !important;
}

// common classes
.label,
.value {
  font-size: 0.8125rem;
}
.label {
  font-weight: bold;
}

.h1 {
  font-weight: bold;
  font-size: 1.25rem;
  line-height: 34px;
}
.h2 {
  font-weight: 600;
  font-size: 1rem;
  line-height: 34px;
}
.h3 {
  font-weight: 600;
  font-size: 0.8125rem;
  line-height: 28px;
}

.pb_5 {
  padding-bottom: 0.5rem;
}
.round_button {
  border-radius: 50px !important;
}
.g-box-shadow {
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}
.shop-dialog {
  mat-dialog-container {
    overflow: hidden !important;
    padding: 0px;
  }
}

// button
.mat-form-field-outline {
  box-shadow: 0px 0px 4px rgb(0 0 0 / 25%) !important;
  border-radius: 5px !important;
}
.mat-form-field-infix {
  border-top: 0 !important;
}

// tabs
.mat-tab-group.mat-accent .mat-ink-bar,
.mat-tab-nav-bar.mat-accent .mat-ink-bar {
  height: 5px;
}

.mat-tab-body {
  background: #f8f8f8;
}
.icon {
  cursor: pointer;
}

.green-btn {
  background-color: #68A616 !important;
  color: #FFFFFF !important;
  letter-spacing: 0.02em;
  font-size: 13px;
}